  box-shadow: inset 0 0 0 0 #202C45;
}

.price .price-item.featured-item .price-footer .btn.btn-custom:hover {
  color: #E81C2E;
  background: #202C45;
  box-shadow: inset 200px 0 0 0 #202C45;
}


/*******************************/
/******** Location CSS *********/
/*******************************/
.location {
