}

.team .team-text {
  position: relative;
  width: 100%;
  padding: 35px 30px 30px 30px;
  text-align: center;
  background: #202C45;
  border-radius: 0 0 5px 5px;
  transition: .5s;
}

.team .team-item:hover .team-text {
  background: #E81C2E;
}

.team .team-text h2 {
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 10px;
}

.team .team-text p {
  margin: 0;
  color: #ffffff;
}

.team .team-social {
  position: absolute;
  width: 100%;
  height: 40px;
  top: -20px;
  left: 0;
  text-align: center;
  font-size: 0;
}

.team .team-social a {
  display: inline-block;
  width: 40px;
  height: 40px;
  margin: 0 3px;
  padding: 7px 0;
  text-align: center;
  font-size: 16px;
  color: #ffffff;
  background: #202C45;
  border-radius: 40px;
  transition: .5s;
}

.team .team-item:hover .team-social a {
  background: #E81C2E;
}

.team .team-social a:hover {
